.page__body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

@keyframes hands-movement {
  from {
    rotate: none;
  }

  to {
    rotate: 360deg;
  }
}

.stopwatch__minute {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin-left: -1.5vmin;
  animation: 3600s steps(60, end) infinite hands-movement;
  position: absolute;
  bottom: 50%;
  left: 50%;
}

.stopwatch__second {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  margin-left: -.75vmin;
  animation: 60s linear infinite hands-movement;
  position: absolute;
  bottom: 50%;
  left: 50%;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  margin-top: -2.5vmin;
  margin-left: -2.5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
}

.stopwatch--speed-up .stopwatch__minute {
  animation: 600s steps(60, end) infinite hands-movement;
}

.stopwatch--speed-up .stopwatch__second {
  animation: 10s linear infinite hands-movement;
}
